#contact {
  color: var(--light-blue);
  text-align: center;
  background: var(--green);
  padding: 4rem 2rem;
}

#contact h2 {
  font-size: 2.5rem;
}

@media (max-width: 500px) {
  #contact h2 {
    font-size: 2rem;
  }

  #contact {
    padding: 2rem 1rem;
  }

  #contact-form input, #contact-form textarea {
    width: calc(100% - 2rem) !important;
  }
}

#contact p {
  color: #fff;
  margin: 2rem 0;
  font-family: nunito-sans;
  font-size: 1.2rem;
}

#contact-form-wrap {
  max-width: 50rem;
  margin: auto;
}

#contact-form {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin: auto;
  display: flex;
}

#contact-form input {
  color: var(--light-blue);
  background: var(--dark-green);
  width: calc(100% - 1rem);
  height: 1rem;
  border: 0;
  outline: 0;
  padding: 1rem;
  font-size: 1.2rem;
}

#contact-form textarea {
  color: var(--light-blue);
  background: var(--dark-green);
  width: calc(100% - 1rem);
  height: 10rem;
  resize: none;
  border: 0;
  outline: 0;
  padding: 1rem;
  font-family: nunito-sans;
  font-size: 1.2rem;
}

#form-submit-button {
  background: var(--dark-green);
  width: auto;
  color: var(--light-blue);
  border: 0;
  outline: 0;
  padding: 1rem 2rem;
  font-family: monomaniac-one;
  font-size: 1.3rem;
  transition: color .1s;
}

#form-submit-button:hover {
  cursor: pointer;
  color: var(--orange);
}

